import React from 'react'
import Link from 'gatsby-link'
import Helmet from "react-helmet";
import Cloud from './computing-cloud.svg'

const NotFoundPage = ({ data }) => (
  <div className="container card">
  <Helmet title={ 'Not found - ' +  data.site.siteMetadata.title } />
    <div className="row">
    <div className="col-12 center">
      <h1>Ups! something went wrong</h1>
      <img src={Cloud}/>
      <p className="left">Unfortunately you tried to go to a page that does not exist. I recommend you to go and visit the <Link to="/">Home</Link> page or <Link to="/journal/">Journal</Link> one.</p>
    </div>
    </div>
  </div>
)

export default NotFoundPage

export const query = graphql
`query NotFoundQuery {
  site {
    siteMetadata {
      title
    }
  }
}`



